.Joke{
    display: flex;
    border-bottom: 2px solid #eeeeee ;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    padding: 1rem;
}

.joke-buttons{
    display: flex;
    margin-right: 1rem;
    align-items: center;
    justify-content: center;
    width: 15%;
}

.Joke-text{
    width: 75%;
    font-size: 1.2rem;
}

.Joke-votes{
    display: inline-block;
    width:50px;
    height: 50px ;
    line-height: 50px;
    border-radius: 50%;
    font-size: 20px;
    font-weight: 300;
    border: 3px solid ;
    text-align: center;
    flex-shrink: 0;
    box-shadow: 0 19px 38px rgba(0,0,0,0.3), 0 15px 12px rgba(0,0,0,0.1);
}

.fa-arrow-up, .fa-arrow-down {
   font-size: 1.5em;
   margin: 10px ;
   cursor: pointer;
   transition: all 0.3s;
}

.fa-arrow-up:hover{
    animation-name: move-up;
    animation-duration: 0.7s;
    animation-timing-function: ease-out;
    color: green;
}

@keyframes move-up {
    0%,
    50%,
    100% {
      transform: translateY(0);
    }
    25%,
    75% {
      transform: translateY(-3px);
    }
  }

  .fa-arrow-down:hover{
    animation-name: move-down;
    animation-duration: 0.7s;
    animation-timing-function: ease-out;
    color:red;
}

@keyframes move-down {
    0%,
    50%,
    100% {
      transform: translateY(0);
    }
    25%,
    75% {
      transform: translateY(3px);
    }
  }


.Joke-smiley{
    font-size: 3rem;
    margin-left: auto;
    border-radius: 50%;
    box-shadow: 0 19px 38px rgba(0,0,0,0.3), 0 15px 12px rgba(0,0,0,0.1);
}

.Joke-smiley:hover{
  animation-name: spin;
  animation-duration: 0.7s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
}

@keyframes spin{
    0%{
        transform:rotate(0deg)
    }

    30%{
        transform:rotate(20deg)
    }

    60%{
        transform:rotate(-10deg)
    }

    80%{
        transform:rotate(10deg)
    }

    100%{
        transform:rotate(0deg)
    }
}